import "../../Style/NavbarStyle.css";
import Logo from "../../../assets/Images/logo.png";
import { NavLink, useLocation } from "react-router-dom";
import { useState } from "react";

const Navbar = () => {
  const location =useLocation();
  const [isOpen, setIsOpen] = useState(false);
  const isActive = /^\/(\d+)?$/.test(location.pathname);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };
  const ToggleNavBar = () => {
    setIsOpen((prev) => !prev);
  };
  return (
    <nav className="nav">
      <div className="logo">
        <img src={Logo} alt="logo" />
      </div>
      <ul className={`navbar ${isOpen ? "show-navbar" : "hide-navbar"}`}>
        <li>
        <NavLink 
      onClick={() => { scrollToTop(); ToggleNavBar(); }} 
      to="/" 
      className={isActive ? "active" : ""}
    >
      الرئيسيه
    </NavLink>

        </li>
        <li>
          {" "}
          <NavLink onClick={()=>{scrollToTop(); ToggleNavBar(); }} to={"/services"}>
            الخدمات
          </NavLink>
        </li>
        <li>
          {" "}
          <NavLink onClick={()=>{scrollToTop(); ToggleNavBar(); }} to={"/Offers"}>
            العروض الترويجية
          </NavLink>
        </li>
        <li>
          {" "}
          <NavLink onClick={()=>{scrollToTop(); ToggleNavBar(); }} to={"/Guide"}>
            الدليل
          </NavLink>
        </li>
        <li>
          {" "}
          <NavLink onClick={()=>{scrollToTop(); ToggleNavBar(); }} to={"/Support"}>
            الدعم الفني
          </NavLink>
        </li>
      </ul>
      <div  className="toggle-navbar">
      <input checked={isOpen} onClick={ToggleNavBar} className="menu-trigger hidden" id="togglenav" type="checkbox" />
        <label  className="burger-wrapper" htmlFor="togglenav">
          <div className="hamburger"></div>
        </label>
      </div>
    </nav>
  );
};

export default Navbar;
